<template>
  <div class="page-home page">

    <div class="header">
      <div class="wrapper">
        <div class="index" ref="index"></div>
      </div>
    </div>
    <div class="footer">
      <div class="wrapper">
        <p>中共十八大后，中央领导层交替，新旧两任执政党总书记均称：腐败问题解决不好将会“亡党亡国”。</p>
        <p>转眼十九大在即，五年来反腐进展如何？从2012年“新人带来新气象”，到今天“反腐进入深水区”，我们搜集了十八大以来落马的中管干部，邀您一起检视反腐“成绩单”。</p>
      </div>
    </div>

  </div>
</template>
<script>
import animateObj from './page-title.js'
export default {
  name: 'pageHome',
  data () {
    return {
      svg: null,
      animateObj: animateObj
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.animateObj.init(this.$refs.index)
      this.animateObj.start()
    })
  }
}
</script>
<style scoped lang="scss">

.header{
  height: 370px;
  background: #5b1112;
}
.footer p {
  max-width: 560px;
  margin: 0 auto;
  font-size: 16px;
  line-height: 1.6;
  margin-bottom: 30px;
    &:first-child{
      margin-top: 60px;
    }
}
.header .wrapper,
.header .index{
  height: 100%;
}
</style>



